import './Header.less'
import {Button} from "antd";
import {UserOutlined} from "@ant-design/icons";

function Header(){
    return(
        <div className='header'>
            <div className="left"></div>
            <div className="right">
                <Button type='link'><UserOutlined />登录</Button>
            </div>
        </div>
    )
};
export default Header
